<template>
  <div class="loginBox">
    <div id="myLogin">
      <img src="../assets/logo.png" style="width: 50px;height: 50px">
      <router-view></router-view>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'Ind'
  }
</script>

<style>
  #myLogin {
    position: relative;
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
    top: 30px;
    margin: 0;
    padding: 0;
  }

  .loginBox {
    position: absolute;
    left: 38%;
    top: 10%;
    width: 400px;
    height: 500px;
    border-radius: 4px;
    border: #D7DAE2 1px solid;
    box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
  }
</style>



