<template>
  <div style="margin-top: 5px">
    <el-radio-group v-model="radio1" size="mini" @change="changeState(radio1)">
      <el-radio-button label="登录"></el-radio-button>
      <el-radio-button label="注册"></el-radio-button>
    </el-radio-group>

    <!--登录-->
    <div ref="loginBox" style="margin-top: 80px;display: block">
      <el-form :model="formLabelAlign" :label-position="labelPosition" :rules="rules" ref="loginForm"
               label-width="60px" class="inputForm">
        <el-form-item label="账号:" prop="username" style="width: 70%;margin-bottom: 40px">
          <el-input v-model="formLabelAlign.username" size="medium" placeholder="请输入您的账号" clearable></el-input>
        </el-form-item>
        <el-form-item label="密码:" prop="password" style="width: 70%">
          <el-input v-model="formLabelAlign.password" size="medium" placeholder="请输入您的密码" show-password></el-input>
        </el-form-item>
        <div style="margin: 100px;"></div>
        <el-row>
          <el-col :span="4" :offset="4">
            <el-button type="primary" @click="submitForm()" size="mini">登&nbsp;&nbsp;录</el-button>
          </el-col>
          <el-col :span="4" :offset="4">
            <el-button @click="cancelForm()" size="mini">取&nbsp;&nbsp;消</el-button>
          </el-col>
        </el-row>
      </el-form>
    </div>
    <!--注册-->
    <div ref="registerBox" style="margin: 20px;display: none" aria-label="right">
      <el-form :model="formLabelData" status-icon :label-position="labelPosition" :rules="rules" ref="registerForm"
               label-width="84px" class="inputForm2">
        <el-form-item label="注册账号:" prop="registryName" style="width: 80%;margin-bottom: 25px">
          <el-input v-model="formLabelData.registryName" size="mini" placeholder="请输入您的账号" clearable></el-input>
        </el-form-item>
        <el-form-item label="密码:" prop="registryPass" style="width: 80%;margin-bottom: 25px">
          <el-input v-model="formLabelData.registryPass" size="mini" placeholder="请输入您的密码" show-password></el-input>
        </el-form-item>
        <el-form-item label="确认密码:" prop="confirmPass" style="width: 80%;margin-bottom: 25px">
          <el-input v-model="formLabelData.confirmPass" size="mini" placeholder="请输入确认密码" show-password></el-input>
        </el-form-item>
        <el-form-item label="手机号码:" prop="registryTel" style="width: 80%">
          <el-input v-model="formLabelData.registryTel" size="mini" placeholder="请填写您的手机号" clearable></el-input>
        </el-form-item>
        <div style="margin: 40px;"></div>
        <el-row>
          <el-col :span="4" :offset="4">
            <el-button type="primary" @click="registerForm()" size="mini">注&nbsp;&nbsp;册</el-button>
          </el-col>
          <el-col :span="4" :offset="5">
            <el-button @click="resetForm()" size="mini">重&nbsp;&nbsp;置</el-button>
          </el-col>
        </el-row>
      </el-form>
    </div>
  </div>
</template>

<script>
  //定义数组，存放用户
  let userInfo = [];

  export default {
    data() {
      let checkName = (rule, value, callback) => {
        let reg1 = /^[a-zA-Z][0-9A-Za-z]{4,11}$/;
        let reg2 = /^[A-Za-z]{5,12}$/;
        if (!reg1.test(value) && reg2.test(value)) callback(new Error('账号必须是5-12位的字母和数字组合，且第一位必须是字母！'));
        else callback();
      };
      let checkPass = (rule, value, callback) => {
        let reg = /^[0-9A-Za-z][0-9A-Za-z\\._]{5,11}$/;
        if (!reg.test(value)) callback(new Error('密码长度必须为6-12位字符,且第一位不能是特殊字符！'));
        else callback();
      };
      let checkPassAgain = (rule, value, callback) => {
        if (value !== this.formLabelData.registryPass) callback(new Error('两次输入密码不一致!'));
        else callback();
      };
      let checkTel = (rule, value, callback) => {
        let reg = /^1[345678]\d{9}$/;
        if (value) {
          if (!reg.test(value) && !value) callback(new Error('号码格式不正确！'));
          else callback();
        } else {
          callback();
        }
      };
      return {
        radio1: '登录',
        labelPosition: 'right',
        formLabelAlign: {
          username: '',
          password: ''
        },
        formLabelData: {
          registryName: '',
          registryPass: '',
          confirmPass: '',
          registryTel: ''
        },
        rules: {
          username: [
            {required: false}
          ],
          password: [
            {required: false}
          ],
          registryName: [
            {validator: checkName, trigger: 'blur'},
            {required: true, message: '请输入账号!'}
          ],
          registryPass: [
            {validator: checkPass, trigger: 'blur'},
            {required: true, message: '请输入密码!'}
          ],
          confirmPass: [
            {validator: checkPassAgain, trigger: 'blur'},
            {required: true, message: '请再次输入密码!'}
          ],
          registryTel: [
            {validator: checkTel, trigger: 'blur'},
            {required: false}
          ]
        }
      };
    },
    methods: {
      //登录和注协切换
      changeState(radio1) {
        if (radio1 === '注册') {
          this.$refs.loginBox.style.display = 'none';
          this.$refs.registerBox.style.display = 'block';
        } else {
          this.$refs.loginBox.style.display = 'block';
          this.$refs.registerBox.style.display = 'none';
        }
      },
      //提交登录按钮
      submitForm() {
        let _this = this;//箭头函数中的this不再指向vue实例，需要提前转化
        if (!_this.formLabelAlign.username || !_this.formLabelAlign.password) {
          _this.$message({
            showClose: true,
            message: "信息填写不完整，无法提交！",
            type: 'warning'
          });
          return false;
        } else {
          //判断用户名密码是否被注册
          if (userInfo.length > 0) {
            for (let user of userInfo) {
              if (user['username'] === _this.formLabelAlign.username && user['password'] === _this.formLabelAlign.password) {
                _this.$message({
                  showClose: true,
                  message: "登录成功，即将访问百度首页...",
                  type: 'success'
                });
                setTimeout(function () {
                  window.location.href = 'https://www.baidu.com/';
                }, 2000);
              }else {
                _this.$message({
                  showClose: true,
                  message: "用户名或密码错误！",
                  type: 'error'
                });
              }
            }
          } else {
            _this.$message({
              showClose: true,
              message: "用户名或密码错误！",
              type: 'error'
            });
            this.$refs.loginForm.resetFields();
          }
        }
      },
      //取消登录
      cancelForm() {
        this.$refs.loginForm.resetFields();
      },
      //提交注册按钮
      registerForm() {
        let _this = this;//箭头函数中的this不再指向vue实例，需要提前转化
        this.$refs.registerForm.validate((valid) => {
          if (valid) {
            //判断该用户名有没有被注册
            if (userInfo.length > 0) {
              for (let user of userInfo) {
                if (user['username'] === _this.formLabelData.registryName) {
                  _this.$message({
                    showClose: true,
                    message: "该账户已被注册！",
                    type: 'error'
                  });
                } else {
                  //如果校验通过，将数据存放到map中
                  let user = new Map();
                  user["username"] = _this.formLabelData.registryName;
                  user["password"] = _this.formLabelData.registryPass;
                  user["telephone"] = _this.formLabelData.registryTel;
                  userInfo.push(user);
                  _this.$message({
                    showClose: true,
                    message: "注册成功！",
                    type: 'success'
                  });
                  setTimeout(function () {
                    _this.radio1 = "登录";
                    _this.$refs.loginForm.resetFields();
                    _this.$refs.loginBox.style.display = 'block';
                    _this.$refs.registerBox.style.display = 'none';
                  }, 2000);
                }
              }
            } else {
              //如果校验通过，将数据存放到map中
              let user = new Map();
              user["username"] = _this.formLabelData.registryName;
              user["password"] = _this.formLabelData.registryPass;
              user["telephone"] = _this.formLabelData.registryTel;
              userInfo.push(user);
              _this.$message({
                showClose: true,
                message: "注册成功！",
                type: 'success'
              });
              setTimeout(function () {
                _this.radio1 = "登录";
                _this.$refs.loginForm.resetFields();
                _this.$refs.loginBox.style.display = 'block';
                _this.$refs.registerBox.style.display = 'none';
              }, 2000);
            }
          } else {
            _this.$message({
              showClose: true,
              message: "信息填写错误或不完整，无法提交！",
              type: 'warning'
            });
            return false;
          }
        })
      },
      //重置按钮
      resetForm() {
        this.$refs.registerForm.resetFields();
      }
    }
  }
</script>

<style scoped>
  .inputForm {
    position: relative;
    left: 10%;
  }

  .inputForm2 {
    position: relative;
    left: 5%;
  }
</style>



